import React from 'react';
import styled from 'styled-components';
import Navs from '../../molecules/Navs/Navs';
import gamepad from './gamepad.png';
import CartSection from '../../molecules/CartSection/CartSection';
import { NavLink } from 'react-router-dom';
import CartWindow from '../../molecules/CartWindow/CartWindow';
import { connect } from 'react-redux';

class Navbar extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <>
        <Nav>
          <NavLink exact to="/gam3r">
            <img src={gamepad} />
          </NavLink>
          <Navs />
          <CartSection openFunc={this.props.handleOpenCart} />
          <Price>{this.props.cost}$</Price>
        </Nav>
        {this.props.cartOpen ? <CartWindow /> : null}
      </>
    );
  }
}
const Price = styled.label`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
  font-size: 1.5vw;
  align-self: center;
  transform: translateX(-1vw);
  font-family: 'Montserrat', sans-serif;
`;

const mapStateToProps = (state) => {
  const { cost } = state;
  return { cost: cost };
};

const Nav = styled.div`
  width: 100vw;
  height: 12vh;
  background-color: whitesmoke;
  align-content: center;
  display: flex;
  outline: none;
  overflow-y: hidden;

  img {
    height: 12vh;
    width: 12vh;
    margin-left: 2.5vw;
    cursor: pointer;
    transform: scale(0.75);
  }
`;

export default connect(mapStateToProps)(Navbar);
