import React from 'react';
import styled from 'styled-components';
import { delAction } from '../actions/delAction';
import { connect } from 'react-redux';
import dark from './dark-wallpapers.jpg';

const Finish = ({ cost }) => (
  <FinishDiv>
    <p className="p">All: {cost}$</p>
    <button className="button">Buy All</button>
  </FinishDiv>
);

const Checkout = ({ items, cost, delItem }) => {
  return (
    <>
      <Panel>
        <div>
          <p>Name</p>
          <p>Price</p>
        </div>
        {items.map((item) => (
          <div id={item.id} key={item.id}>
            <p>{item.name}</p>
            <p>{item.price}$</p>
            <button>Buy</button>
            <button
              id={item.id}
              onClick={(e) => delItem(e.target.id, item.price)}
            >
              Remove
            </button>
          </div>
        ))}
      </Panel>
      <Finish cost={cost} />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    items: state.items,
    cost: state.cost,
  };
};

const mapDispatchToProps = (dispatch) => ({
  delItem: (id, price) => dispatch(delAction(id, price)),
});

const FinishDiv = styled.div`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
  width: 100vw;
  height: 10vh;
  position: fixed;
  display: flex;
  justify-self: center;
  background-color: whitesmoke;
  margin: 0;
  z-index: 99999;

  p {
    width: 8vw;
    font-size: 1.6vw;
    z-index: 99999;
    font-family: 'Montserrat', sans-serif;
    margin-left: 41vw;
  }

  button {
    width: 8vw;
    height: 4vh;
    margin-top: 3vh;
    z-index: 99999;
    font-family: 'Montserrat', sans-serif;
  }
`;

const Panel = styled.div`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
  width: 100vw;
  height: 78vh;
  overflow: hidden;
  overflow-y: scroll;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url(${dark});
  background-size: cover;

  div {
    color: white;
    width: 82vw;
    margin-left: 9vw;
    height: 8vh;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 1vh;
    border-bottom: solid white 1.5px;

    p {
      font-size: 1.4vw;
      text-align: center;
      font-family: 'Montserrat', sans-serif;
    }

    button {
      width: 12vw;
      height: 8vh;
      font-size: 1.3vw;
      font-family: 'Montserrat', sans-serif;
    }
  }
`;

export default connect(mapStateToProps, mapDispatchToProps)(Checkout);
