import React from 'react';
import logo from './logo.svg';
import './App.css';
import NavbarButton from './components/atoms/NavbarButton/NavbarButton';
import Navs from './components/molecules/Navs/Navs';
import Navbar from './components/organisms/Navbar/Navbar';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import GlobalStyle from './components/GlobalStyle/GlobalStyle';
import Home from './components/Home/Home';
import Games from './components/Games/Games';
import store from './components/store/index';
import { Provider } from 'react-redux';
import Checkout from './components/Checkout/Checkout';
class App extends React.Component {
  state = {
    totalPrice: 99.99,
    cartOpen: false,
  };
  handleOpenCart = async () => {
    await this.setState({ cartOpen: !this.state.cartOpen });
  };
  render() {
    return (
      <>
        <Provider store={store}>
          <GlobalStyle />
          <div className="App">
            <BrowserRouter>
              <Navbar
                cartOpen={this.state.cartOpen}
                price={this.state.totalPrice}
                handleOpenCart={this.handleOpenCart}
              />
              <Switch>
                <Route exact path="/gam3r" component={Home} />
                <Route exact path="/gam3r/games" component={Games} />
                <Route exact path="/gam3r/checkout" component={Checkout} />
              </Switch>
            </BrowserRouter>
          </div>
        </Provider>
      </>
    );
  }
}

export default App;
