import React from 'react';
import styled from 'styled-components';
import Item from '../molecules/Item/Item';
import { GamesLibrary } from './GamesLibrary';
import { addAction } from '../actions/addAction';
import { connect } from 'react-redux';
import dark from './dark-wallpapers.jpg';

const Games = ({ addItem }) => {
  const addItemFunc = async (name, price, id) => {
    await addItem(id, name, price);
  };
  return (
    <Div>
      {GamesLibrary.map((item) => (
        <Item
          key={item.productID}
          image={item.imageSrc}
          name={item.name}
          price={item.price}
          addFunc={() => addItemFunc(item.name, item.price, Math.random())}
        />
      ))}
    </Div>
  );
};

const mapDispatchToProps = (dispatch) => ({
  addItem: (id, name, price) => dispatch(addAction(id, name, price)),
});

const Div = styled.div`
  width: 100vw;
  height: 88vh;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url(${dark});
  background-size: cover;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  overflow-y: scroll;
  grid-gap: 1vh; ;
`;

export default connect(null, mapDispatchToProps)(Games);
