import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import gsap from 'gsap';
import { connect } from 'react-redux';
import { delAction } from '../../actions/delAction';
import { NavLink } from 'react-router-dom';

const CartWindow = ({ products, removeFunc }) => {
  const cartWindow = React.useRef(null);
  useEffect(() => {
    const tl = gsap.timeline();
    const cart = cartWindow.current;

    tl.fromTo(cart, 0.6, { autoAlpha: 0 }, { autoAlpha: 1 });
  });
  return (
    <Window ref={cartWindow}>
      <section>
        {products.map((item) => (
          <div id={item.id} key={item.id}>
            <p>{item.name}</p>
            <button onClick={() => removeFunc(item.id, item.price)}>
              Remove
            </button>
          </div>
        ))}
      </section>
      <NavLink exact to="/gam3r/checkout">
        <button id="checkout">CHECKOUT</button>
      </NavLink>
    </Window>
  );
};

const mapStateToProps = (state) => {
  const { items } = state;
  return { products: items };
};

const mapDispatchToProps = (dispatch) => {
  return {
    removeFunc: (id, price) => dispatch(delAction(id, price)),
  };
};

const Window = styled.div`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
  width: 30vw;
  transform: translateX(69.5vw);
  height: 70vh;
  background-color: whitesmoke;
  border: solid black 1px;
  position: fixed;
  opacity: 0;
  z-index: 999;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  background-color: whitesmoke;
  section {
    ::-webkit-scrollbar {
      border-radius: 50px;
    }
    ::-webkit-scrollbar-thumb {
      background: black;
      border-radius: 50px;
    }
    height: 63vh;
    margin-top: 1vh;
    overflow-y: scroll;
    div {
      display: flex;
      border-bottom: solid black 2px;
      p {
        text-align: center;
        padding-left: 4vw;
        font-size: 1.15vw;
        font-family: 'Montserrat', sans-serif;
      }
      button {
        display: block;
        justify-self: flex-end;
        margin-left: 1vw;
        font-size: 1.4vw;
        font-family: 'Montserrat', sans-serif;
        color: white;
        background-color: hsl(0, 100%, 0%);
        border: none;
        outline: none;
        cursor: pointer;
        height: 6vh;
        margin-top: 1vh;
      }
    }
  }
  #checkout {
    font-family: 'Montserrat', sans-serif;
    height: 5vh;
    width: 8vw;
    border-radius: 50px;
    color: white;
    background-color: hsl(0, 100%, 0%);
    border: none;
    outline: none;
  }
`;

export default connect(mapStateToProps, mapDispatchToProps)(CartWindow);
