import React from 'react';
import styled from 'styled-components';

const Item = ({ id, name, price, image, addFunc }) => {
  return (
    <It>
      <Image image={image} />
      <h2>{name}</h2>
      <h3>Cena : {price}</h3>
      <button onClick={addFunc}>Add to Cart</button>
    </It>
  );
};

export default Item;

const It = styled.div`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
  width: 25vw;
  height: 32vw;
  border: 1px solid black;
  background-color: whitesmoke;
  margin-left: 4vw;
  text-align: center;

  h2 {
    font-size: 2vw;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
  }
  h3 {
    font-size: 1.2vw;
    margin: 0;
    margin-top: 2vh;
    font-family: 'Montserrat', sans-serif;
  }
  button {
    font-size: 1vw;
    margin-top: 2vh;
    cursor: pointer;
    background: hsl(0, 80%, 0%);
    color: white;
    border: none;
    border-radius: 50px;
    width: 8vw;
    height: 4vh;
    font-family: 'Montserrat', sans-serif;
    outline: none;
  }
`;

const Image = styled.div`
  width: 18vw;
  height: 20vw;
  background-image: url(${(props) => props.image});
  background-color: blue;
  background-size: cover;
  margin-left: 3.5vw;
`;
