import React from 'react';
import NavbarButton from '../../atoms/NavbarButton/NavbarButton';
import styled from 'styled-components';
import { NavLink } from 'react-router-dom';

const ElementsArray = [
  {
    text: 'Games',
    path: '/gam3r/games',
  },
  {
    text: 'Consoles',
    path: '/gam3r/consoles',
  },
  {
    text: 'Other',
    path: '/gam3r/other',
  },
  {
    text: 'About',
    path: '/gam3r/about',
  },
];

const Navs = () => (
  <Div>
    {ElementsArray.map((item) => (
      <NavLink key={item.text} exact to={item.path}>
        <NavbarButton>{item.text}</NavbarButton>
      </NavLink>
    ))}
  </Div>
);

const Div = styled.div`
  display: block;
`;

export default Navs;
