import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import bc1 from './bc1.jpg';
import gsap from 'gsap';
import { useHistory } from 'react-router-dom';

const Home = () => {
  const div = React.useRef(null);
  const history = useHistory();
  const handleClick = async () => {
    await history.push('/gam3r/games');
  };
  useEffect(() => {
    const wrapper = div.current;
    const title = div.current.children[0];
    const button = div.current.children[1];
    const tl = gsap.timeline();

    tl.to(wrapper, 0.6, { opacity: 1 }).to([title, button], 0.6, {
      opacity: 1,
    });
  });
  return (
    <Div ref={div}>
      <h1>GAM3R</h1>
      <button onClick={handleClick}>GO SHOPPING</button>
    </Div>
  );
};

const Div = styled.div`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
  width: 100vw;
  height: 88vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(${bc1});
  background-size: cover;
  background-attachment: fixed;
  overflow-y: hidden;
  overflow-x: hidden;
  filter: grayscale(1);
  opacity: 0;
  font-family: 'Montserrat', sans-serif;

  h1 {
    color: whitesmoke;
    font-size: 10vw;
    text-align: center;
    opacity: 0;
  }
  button {
    display: block;
    margin: auto auto;
    font-family: 'Montserrat', sans-serif;
    width: 20vw;
    height: 8vh;
    font-size: 1.6vw;
    opacity: 0;
    cursor: pointer;
  }
`;

export default Home;
